import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AuthProvider } from '../../providers/auth/auth';
import { LoginPage } from '../login/login'

/**
 * Generated class for the PropertySubmissionPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-property-submission',
  templateUrl: 'property-submission.html',
})
export class PropertySubmissionPage {

  public authUser: any;

  constructor(public navCtrl: NavController, public navParams: NavParams, public authData: AuthProvider,) {
    this.authUser = this.authData.getLoggedInUser();
    if (this.authUser) {
      console.log("Authed")
    } else {
      this.navCtrl.setRoot(LoginPage)
    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PropertySubmissionPage');
  }

}



