import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AuthProvider } from '../../providers/auth/auth';
import { LoginPage } from '../login/login'

/**
 * Generated class for the MainBuySearchPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-main-buy-search',
  templateUrl: 'main-buy-search.html',
})
export class MainBuySearchPage {

  public authUser: any;

  constructor(public navCtrl: NavController, public navParams: NavParams, public authData: AuthProvider,) {
    this.authUser = this.authData.getLoggedInUser();
    if (this.authUser) {
      console.log("Authed")
    }
    else {
      this.navCtrl.setRoot(LoginPage)
    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MainBuySearchPage');
  }

}



